import React, { useContext, useState } from 'react';
import PropTypes from 'prop-types';
import { ReactComponent as PersonIcon } from 'assets/icons/personIcon.svg';
import {
  UserSettingsWrapper,
  SettingsHeader,
  HeaderUsername,
  HeaderUserImage,
  NoUserMessage,
  HeaderUserWrapper,
  PageName,
} from './UserSettings.styles';
import SettingsMainContentBox from 'components/organisms/SettingsMainContentBox/SettingsMainContentBox';
import SettingsModals from 'components/organisms/SettingsModals/SettingsModals';
import { useSelector } from 'react-redux';

const UserSettings = (props) => {
  const { user: currentUser } = useSelector((state) => state.auth);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalType, setModalType] = useState('');

  const closeModal = () => {
    setIsModalOpen(false);
  };
  const openModal = () => setIsModalOpen(true);

  return (
    <>
      <UserSettingsWrapper>
        {currentUser ? (
          <>
            <SettingsHeader>
              <PageName>Ustawienia</PageName>
              <HeaderUserWrapper>
                <HeaderUserImage>
                  <PersonIcon />
                </HeaderUserImage>
                <HeaderUsername>{currentUser.username}</HeaderUsername>
              </HeaderUserWrapper>
            </SettingsHeader>
            <SettingsMainContentBox
              setModalType={setModalType}
              openModal={openModal}
              currentUser={currentUser}
            />
          </>
        ) : (
          <NoUserMessage>Nie jeste zalogowany</NoUserMessage>
        )}
        <SettingsModals
          modalType={modalType}
          isOpen={isModalOpen}
          handleOnClose={closeModal}
        />
      </UserSettingsWrapper>
    </>
  );
};

UserSettings.propTypes = {};

export default UserSettings;
