export const validateRegistration = (username, password, email) => {
  
  const validateEmail = (email) => {
    const re = /\S+@\S+\.\S+/;
    return re.test(email);
  };

  const emailForm = validateEmail(email);

  if (!username || !password || !email) {
    return 'Wszystkie pola powinny zosta wypenione';
  } else if (username.length < 3 || username.length > 20) {
    return 'Nazwa uytkownika powinno skada si z minimalnie 3, a maksymalnie 20 znakw';
  } else if (password.length < 6 || password.length > 40) {
    return 'Haso powinno skada si z minimalnie 6, a maksymalnie 40 znakw';
  } else if (!emailForm) {
    return 'Niepoprawny adres email';
  } else if (email.length > 50) {
    return 'Email powinien skada si z maksymalnie 50 znakw';
  }
};

export const validateLoginMessage = 'Wszystkie pola powinny zosta wypenione';

export const validateLoginState = (username, password) => {
  return !username || !password;
};
