import React from 'react';
import PropTypes from 'prop-types';
import Modal from 'components/atoms/Modal/Modal';
import { ModalWrapper } from './SettingsModals.style';
import ExitButton from 'components/atoms/ExitButton/ExitButton';
import Input from 'components/atoms/Input/Input';
import Button from 'components/atoms/Button/Button';
import StyledLabel from 'components/atoms/StyledLabel/StyledLabel';

const SettingsModals = ({
  isOpen,
  handleOnClose,
  modalType,
}) => {
  return (
    <Modal
      shouldBeClosedOnOutsideClick={true}
      isOpen={isOpen}
      handleOnClose={handleOnClose}
    >
      {modalType === 'CHANGE_EMAIL' ? (
        <ModalWrapper>
          <ExitButton />
          <StyledLabel>
            Nowy email:
            <Input placeholder="example@xx.com" marginTop="1em" type="email" />
          </StyledLabel>
          <StyledLabel>
            Haso:
            <Input placeholder="password" marginTop="1em" type="password" />
          </StyledLabel>
          <Button>Zmie</Button>
        </ModalWrapper>
      ) : (
        <ModalWrapper>
          <ExitButton />
          <StyledLabel>
            Nowe haso:
            <Input placeholder="new password" marginTop="1em" type="password" />
          </StyledLabel>
          <StyledLabel>
            Powtrz haso:
            <Input placeholder="new password" marginTop="1em" type="password" />
          </StyledLabel>
          <StyledLabel>
            Stare haso:
            <Input placeholder="old password" marginTop="1em" type="password" />
          </StyledLabel>
          <Button>Zmie</Button>
        </ModalWrapper>
      )}
    </Modal>
  );
};

SettingsModals.propTypes = {};

export default SettingsModals;
